import {AContainer} from "./abstract/a-container";
import {Box} from "./box";
import {EViewSize} from "../component/enum/e-view-size";
import {IRenderable} from "../core/interface/i-renderable";
import {EventEmitter} from "../core/event-emitter";
import jquery = require("jquery");
import "./assets/css/vertical-splitter.css!";

export interface IVerticalSplitterConfig{
	minSize?:number;
	maxSize?:number;
	stepSize?:number;
	size?:number;
}

/**
 * <img width="100%" src="../../doc_assets/vertical-splitter-example.png">
 * Linha horizontal que representa uma quebra de linha podendo conter cor e tamanho
 * @memberof module:mvcomponents/container
 * 
 * @example
 *  import {VerticalSplitter,Box,Form} from "mvcomponents/container";
 *  import {Form} from "mvcomponents/container";
 *  import {EVerticalAlign} from "mvcomponents/component";
 *
 *  let container:Box = new Box();
 *  Box.setSize(12);
 *  ...
 *  let vsplitter:VerticalSplitter = new VerticalSplitter();
 *  vsplitter.setSize(12);
 *  vsplitter
 *              .append(tmpbox,{size:3,minSize:0,stepSize:3,maxSize:3})
 *              .append(tmpbox2,{minSize:1,stepSize:3}).append(tmpbox3);
 *  Box.append(vsplitter);
 */
export class VerticalSplitter extends AContainer<IRenderable>{
	public onCollapse:EventEmitter<any> = new EventEmitter();
	public onExpande:EventEmitter<any> = new EventEmitter();
	constructor(){
		super("div","");
		let $this = this;
		jquery(this.element)
			.addClass("VerticalSplitter")
			.on("click",".splitter-content .splitter-divisor-content.is-right",function (){
				//console.log('expand');
				if( jquery(this).parent().hasClass("is-right")){                
					let element = jquery(this).parents(".splitter-content:first");                   
					let nextItem = element.next();
					let configItemAtual:IVerticalSplitterConfig = $this.getSplitterContentConfig(element[0]);
					let configNextIten:IVerticalSplitterConfig = Object.assign({},{
						size:12
						,minSize:0
						,maxSize:12
						,stepSize:1
					});

					if(nextItem.length){
						configNextIten = $this.getSplitterContentConfig(nextItem[0]);
					}

					if(
						(configItemAtual.size + configItemAtual.stepSize) <= configItemAtual.maxSize 
						&& 
						(configNextIten.size - configItemAtual.stepSize) >= configNextIten.minSize 
						&& 
						(configNextIten.size - configItemAtual.stepSize) <= configNextIten.maxSize
					){   
						let isOkToExpand:boolean=true;                                      
						if(nextItem.length){                            
							if((configNextIten.size-configItemAtual.stepSize) >= configItemAtual.minSize){
								nextItem.attr("data-size",(configNextIten.size-configItemAtual.stepSize));
							}else{
								isOkToExpand=false; 
							}                        
							//console.log(element.next());
						}
						if(isOkToExpand){
							$this.onExpande.emit(null).done(
							()=>{
								element.attr("data-size",(configItemAtual.size+configItemAtual.stepSize));
								$this.reRender();
							});
						}
					}
				}
			})
			.on("click",".splitter-content .splitter-divisor-content.is-left",function (){
				//console.log('collapse');
				if( jquery(this).parent().hasClass("is-right")){                 
					let element = jquery(this).parents(".splitter-content:first");
					let configItemAtual:IVerticalSplitterConfig = $this.getSplitterContentConfig(element[0]);
					if((configItemAtual.size-configItemAtual.stepSize) >= configItemAtual.minSize){
						$this.onCollapse.emit(null).done(
							()=>{
								element.attr("data-size",(configItemAtual.size-configItemAtual.stepSize));
								let nextItem = element.next();
								if(nextItem.length){
									let elementSizeNext:number = parseInt(nextItem.attr("data-size"));
									nextItem.attr("data-size",(elementSizeNext+configItemAtual.stepSize));
									//console.log(element.next());
									if((configItemAtual.size-configItemAtual.stepSize)===0){
										nextItem.removeClass("aux-is-hidden");
									}
								}
								$this.reRender();                            
						});
					}
				}
			})
			.on("click",".splitter-content .splitter-divisor-content.is-right",function (){
				//console.log('expand-2');
				if( jquery(this).parent().hasClass("is-left")){
					let element = jquery(this).parents(".splitter-content:first");   
					let prevItem = element.prev();                 
					let configItemAtual:IVerticalSplitterConfig = $this.getSplitterContentConfig(element[0]);
					let configPrevIten:IVerticalSplitterConfig = Object.assign({},configItemAtual);
					if(prevItem.length){
						configPrevIten = $this.getSplitterContentConfig(prevItem[0]);
					}
					if((configItemAtual.size-configPrevIten.stepSize) >= configItemAtual.minSize){  
						$this.onExpande.emit(null).done(
							()=>{
								element.attr("data-size",(configItemAtual.size-configPrevIten.stepSize));                                
								if(prevItem.length){
									
									prevItem.attr("data-size",(configPrevIten.size+configPrevIten.stepSize));
									//console.log(element.next());
								}
								element.addClass("aux-is-hidden");
								$this.reRender();
							}
						);
						
					}
				}
			});
	}

	/**
	 * Método usado para adicionar um item ao container do VerticalSplitter.
	 * @param item {IRenderable} 
	 * @param config {IVerticalSplitterConfig} não é necessário especificar o tamanho do ultimo item.
	 * @example 
	 * let vsplitter:VerticalSplitter = new VerticalSplitter();
	 * let tmpbox:Box = new Box();
	 * let tmpbox2:Box = new Box();
	 * let tmpbox3:Box = new Box();
	 * tmpbox.setCssProperties({"background-color":"#f3f3f3","height":"600px"});
	 * vsplitter.setSize(12);
	 * vsplitter.append(tmpbox,{
	 *  size:3
	 *  ,minSize:0
	 *  ,stepSize:3
	 *  ,maxSize:3
	 * }).append(tmpbox2,{
	 *  minSize:1
	 *  ,stepSize:3
	 * }).append(tmpbox3);
	 *
	 * 
	 * @return {VerticalSplitter}
	 */
	public append(item: IRenderable,config?:IVerticalSplitterConfig):this{        
		return this.insertSplitterItem(item,"append",config);
	}
	public attached():void{

	}
	public prepend(item: IRenderable,config?:IVerticalSplitterConfig):this{        
		return this.insertSplitterItem(item,"prepend",config);
	}
	private getSplitterContentConfig(element:Element):IVerticalSplitterConfig{
		let tmpElement = jquery(element);
		let configObject:IVerticalSplitterConfig={
				minSize:parseInt(tmpElement.attr("data-min-size"))
				,maxSize:parseInt(tmpElement.attr("data-max-size"))
				,stepSize:parseInt(tmpElement.attr("data-step-size"))
				,size:parseInt(tmpElement.attr("data-size"))
		};        
		return configObject;
	}
	private insertSplitterItem(item: IRenderable,type:string,config?:IVerticalSplitterConfig):this{

		let defaultConfig:IVerticalSplitterConfig = <IVerticalSplitterConfig>jquery.extend({},{
			minSize:0
			,maxSize:12
			,stepSize:1
			,size:0
		},config);
		let lgSize:number = defaultConfig.size || this.getRemaingAmountSize();
		(<Box>item)
			.addStyleName("splitter-content aux-is-hidden")
			.setAttributes({
					"data-size":lgSize
					,"data-default-size":defaultConfig.size||0
					,"data-min-size":defaultConfig.minSize
					,"data-max-size":defaultConfig.maxSize
					,"data-step-size":defaultConfig.stepSize
				});
		let boxdivsright = new Box().addStyleName("box-splitter-divisor-content is-right");
		boxdivsright
				.prepend(new Box().addStyleName("splitter-divisor-content is-right"))
				.prepend(new Box().addStyleName("splitter-divisor-content is-left"));

		let boxdivsleft = new Box().addStyleName("box-splitter-divisor-content is-left");
		boxdivsleft
				.prepend(new Box().addStyleName("splitter-divisor-content is-right"));
				//.prepend(new Box().addStyleName("splitter-divisor-content is-left"))
		(<Box>item)
				.append(boxdivsright)
				.prepend(boxdivsleft);
		//super[type](new Box().addStyleName("splitter-divisor-content"));   
		super[type](item);
		return this.reCalculate().reRender();
	}
	private getRemaingAmountSize():number{
		let nsize:number = 12;
		jquery(this.element).children().filter(".splitter-content").each(function (indx:number){
			nsize -= parseInt(jquery(this).attr("data-default-size"));            
		});
		return nsize;
	}
	private reCalculate():this{
		let ncount:number = 0;        
		jquery(this.element).children().filter(".splitter-content").each(function (indx:number){
			let nsize:number = parseInt(jquery(this).attr("data-default-size")); 
			if(nsize < 1){
				ncount++;
			}            
		});
		let naverage:number = 1;
		if(ncount && this.getRemaingAmountSize()){
			naverage = this.getRemaingAmountSize()/ncount;
		}
		let nremant = 0;
		if(naverage !== Number(`${naverage}`)){
			naverage = Number(`${naverage}`);
			nremant++;
		}
		jquery(this.element)
			.children()
			.filter(".splitter-content")
			.each(function (indx:number){
				var element = jquery(this);
				let nsize:number = parseInt(jquery(this).attr("data-default-size")); 
				if(nsize < 1){
					jquery(this).attr("data-size",(naverage+nremant));
					nremant=0;
				}            
			});
		return this;
	}
	private reRender():this{
		let $this = this;
		jquery(this.element).children().filter(".splitter-content").each(function (indx:number){
			var element = jquery(this);
			let atualElementConfig:IVerticalSplitterConfig = $this.getSplitterContentConfig(this);          
			var elementclass:string = element.attr("class"); 
			elementclass = elementclass.replace(/col-xs-\d{1,2}/g,"");
			if(atualElementConfig.size === 0){
				elementclass +=" col-xs-1 is-hidden";
			}else{
				elementclass = elementclass.replace(" is-hidden","");
				elementclass += ` col-xs-${atualElementConfig.size}`;
			}
			if(atualElementConfig.maxSize === atualElementConfig.size){
				elementclass += " is-max";
			}else{
				elementclass = elementclass.replace(" is-max","");
			}
			if(atualElementConfig.minSize === atualElementConfig.size){
				elementclass += " is-min";
			}else{
				elementclass = elementclass.replace(" is-min","");
			}
			element.attr("class","")
				.addClass(`${elementclass} col-xs-${atualElementConfig.size}`);                
		});        
		return this;
	}
}
